<template>
  <div class="bofenghan">
    <div class="title">
      <h1>波峰焊温度监控</h1>
    </div>
    <el-table
    class="myTable"
    :data="tableData"
    style="width: 100%"
    :row-class-name="tableRowClassName">
    <el-table-column
      prop="EQUIPMENTCODE"
      label="设备代号">
    </el-table-column>
    <el-table-column
      prop="EQUIPMENTNAME"
      label="设备名称">
    </el-table-column>
     <el-table-column
      prop="EQUIPMENTPOSITION"
      label="设备位置">
    </el-table-column>
    <el-table-column
      prop="item1"
      label="锡炉设置温度误差">
    </el-table-column>
    <el-table-column
      prop="item2"
      label="预热1设置温度误差">
    </el-table-column>
    <el-table-column
      prop="item3"
      label="预热2设置温度误差">
    </el-table-column>
    <el-table-column
      prop="item4"
      label="预热3设置温度误差">
    </el-table-column>
    <el-table-column
      prop="item5"
      label="传输速度误差">
    </el-table-column>
    <el-table-column
      prop="message"
      label="备注"
      width="180">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  name: 'Bofenghan',
  data () {
    return {
        url:"",
        intervalId:"",
        tableData: []
    }
  },
  methods:{
    tableRowClassName({row, rowIndex}) {
        if (row.result == "0") {
          return 'warning';
        } 
        return '';
      },
    getNewData(){
      console.log("");
	    this.url ='http://' + window.location.host;
	    console.log(this.url);
      this.$axios.get(`${this.url}/dafeng_mes/plan3/earlyWarning.do?EQUIPMENTCATEGORYID=1102`)
      .then(res => {
          console.log(res);
          this.tableData = res.data.listEquipment;     
      })
      .catch(err =>{
        console.log(err);
      })
    },
  },
  created(){ 
      this.getNewData();
      //定时请求数据--10min
      this.intervalId = setInterval(() => {
        this.getNewData();    
      },600000)   
  },
   beforeDestroy () {
      clearInterval(this.intervalId)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.title{
  text-align: center;
  color: #fff;
}
.warning {
  background-color: #cf1322;
  -webkit-animation: twinkling 1s infinite ease-in-out;
  animation: twinkling 1s infinite ease-in-out;
} 
.el-table{
  color: #fff;
}
.el-table th, .el-table tr {
  color: #fff;
  background-color: #000 !important;
  opacity:0.9;
}
.animated{ 
  -webkit-animation-duration: 1s; 
  animation-duration: 1s; 
  -webkit-animation-fill-mode: both; 
  animation-fill-mode: both; 
} 
@-webkit-keyframes twinkling{ 
0%{
  background-color: #000; 
  opacity: 0.8; 
} 
100%{ 
  background-color: #cf1322; 
  opacity: 0.9;
} 
} 
@keyframes twinkling{ 
0%{
  background-color: #000;  
  opacity: 0.8; 
} 
100%{
  background-color: #cf1322;  
  opacity: 0.9; 
} 
}
</style>



